.App {
  width: 100%;
  text-align: center;
}

.content-area {
  max-width: 1024px;
  margin: auto;
  height: 100%;
}

.content-area h2 {
  font-size: 40px;
  margin-bottom: 16px;
  margin-top: 16px;
  padding: 0;
}

.content-area h3 {
  font-size: 20px;
  margin-top: 16px;
  margin-bottom: 16px;
  padding: 0;
}

.content-area hr {
  margin: 24px;
}

.navigation {
  width: 100%;
  height: 64px;
  background-color: #483d8b;
  font-family: 'Exo 2', 'monospace';
  font-size: 16px;
  font-weight: 400;
  color: #fff;
  margin-bottom: 32px;
  text-align: left;
}

.navigation span {
  display: inline-block;
  padding: 12px;
  padding-left: 64px;
  font-size: 24px;
  margin: 0;
}

.navigation- ul {
  display: inline;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.navigation li {
  float: right;
}

.navigation .item {
  display: block;
  float: right;
  padding: 16px;
  padding-top: 20px;
  height: 28px;
  color: #fff;
  text-decoration: none;
  transition: 0.1s;
  background-color: #483d8b;
}

.navigation .item:hover {
  background-color: #6f64b8;
  transition: 0.1s;
}

.navigation .active,
.navigation .active:hover {
  background-color: #2e2950;
}

.fade-enter {
  opacity: 0.01;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}
.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

div.transition-group {
  position: relative;
}
section.route-section {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.profile {
  text-align: left;
  display: inline-block;
}

.profile-pic {
  border-radius: 50%;
  height: 320px;
  float: left;
  margin-left: 64px;
  margin-right: 64px;
}

.social-ul {
  display: inline;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.social-ul li {
  display: inline-block;
  text-decoration: none;
  padding: 16px;
}

.resume {
  display: inline-block;
  padding: 0;
  text-align: center;
}